import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import Modal from 'react-modal';
const customStyles = {
  content : {
    top                   : '50%',
    left                  : '50%',
    right                 : 'auto',
    bottom                : 'auto',
    marginRight           : '-50%',
    transform             : 'translate(-50%, -50%)'
  }
};
Modal.setAppElement('#root')

class App extends Component {
  constructor() {
    super();
    this.state = {
      modalIsOpen: false,
      link: ''
    };
    this.openModal = this.openModal.bind(this);
    this.afterOpenModal = this.afterOpenModal.bind(this);
    this.closeModal = this.closeModal.bind(this);
  }

  openModal() {
    this.setState({modalIsOpen: true});
  }

  afterOpenModal() {
    // references are now sync'd and can be accessed.
    this.subtitle.style.color = '#f00';
  }

  closeModal() {
    this.setState({modalIsOpen: false});
  }

  handleChange(event) {
    this.setState({link: event.target.value});
  }

  formSubmitted(e) {
    e.preventDefault();
    if (this.state.link.indexOf('facebook.com') < 0) {
      alert('not facebook link. Submit a proper link');
    }
    else {
      this.setState({name: this.state.link.split('?')[0].split('facebook.com')[1]})
      this.openModal();
    }
  }
  render() {
    return (
      <div className="App">
        <header className="App-header">
            <h1 className="App-title">Welcome to facebook account Hacker pro</h1>
          </header>
          <h1> Input the profile link of facebook in following field </h1>
        <form className="App-intro" onSubmit={this.formSubmitted.bind(this)} action="">
          <input type="text" name="title" value={this.state.link} onChange={this.handleChange.bind(this)}/>
          <input type="submit" value="submit" />
        </form>

        <Modal
          isOpen={this.state.modalIsOpen}
          onAfterOpen={this.afterOpenModal}
          onRequestClose={this.closeModal}
          style={customStyles}
          contentLabel="Trying to hack"
        >

          <h2 ref={subtitle => this.subtitle = subtitle}>Trying to hack {this.state.name}</h2>
          <div>Sending message to {this.state.name} that "Shankar is trying to hack their account."</div>
          <div> Shame on you </div>
          <div><a href="http://akk.li/pics/anne.jpg"> Click here to cancel and promise to never do this again </a></div>
        </Modal>
      </div>
    );
  }
}

export default App;



